import {FC, useEffect} from 'react'
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "../../../Redux/Store";
import {ResponseTypeCardsPacksData} from "../../../API/Api";
import PacksComponent from "./PacksComponent";
import {addPacksThunk, deletePackThunk, getPacksThunk} from "../../../Redux/PacksPageReducer/PacksPageReducer";
import {RequestStatusType} from "../../../Redux/AuthReducer/AuthReducer";
import Spinner from "../../../Common/preloader/Spinner";

interface Props {

}

const PacksContainer: FC<Props> = ({
                                       ...props
                                   }) => {
    const data = useSelector<AppRootStateType, ResponseTypeCardsPacksData[] | null >(state => state.packsPage.cardPacks)
    const error = useSelector<AppRootStateType, string | undefined>( state => state.packsPage.error)
    const status = useSelector<AppRootStateType,  RequestStatusType> (state => state.packsPage.status)
    const isDisabled = useSelector<AppRootStateType, boolean>( state => state.packsPage.isDisabled)
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(getPacksThunk())
    },[])


    const onAddedPack = () => {
        dispatch(addPacksThunk({}))
    }

    const onDeletePack = (id: string) => {
        dispatch(deletePackThunk(id))
    }




    return (<div>
        <h1>Packs Page</h1>
        {
            status === 'loading'
                ? <Spinner/>
                : status === 'failed'
                ? <div>
                    <h1>{error}</h1></div>
                : ''
        }
        <div>



            <button onClick={onAddedPack}
                    disabled = {isDisabled}
            >Add new Packs</button>
            {
                data?.map(item => {
                    return <div key={item._id}>
                        <PacksComponent cardPacks={item}
                                        onDeletePack = {onDeletePack}  />
                    </div>
                })
            }
        </div>

    </div>)

}


export default PacksContainer